"use client";

import { useEffect, useState } from "react";
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from "recharts";
import { Music, TrendingUp, Calendar } from "lucide-react";

type Track = {
  id: number;
  title: string;
  artist: string;
};

type TopTrack = {
  track: Track;
  playCount: number;
};

type Stats = {
  topTracks: TopTrack[];
  totalListens: number;
  uniqueTracks: number;
  listensByDay: Record<string, number>;
};

export default function StatsPage() {
  const [stats, setStats] = useState<Stats | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchStats();
  }, []);

  const fetchStats = async () => {
    try {
      const res = await fetch("/api/stats");
      if (!res.ok) {
        throw new Error(`HTTP error! status: ${res.status}`);
      }
      const data = await res.json();
      setStats(data);
    } catch (error) {
      console.error("Erreur chargement stats:", error);
      setStats(null);
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return <div className="text-center py-12">Chargement...</div>;
  }

  if (!stats) {
    return <div className="text-center py-12">Erreur de chargement</div>;
  }

  const chartData = Object.entries(stats.listensByDay).map(([date, count]) => ({
    date: new Date(date).toLocaleDateString("fr-FR", { day: "numeric", month: "short" }),
    écoutes: count,
  }));

  return (
    <div className="max-w-7xl mx-auto">
      <h1 className="text-3xl font-bold mb-6">Statistiques d'écoute</h1>

      {/* Statistiques générales */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-8">
        <div className="bg-gradient-to-br from-blue-500 to-blue-700 rounded-lg p-6 text-white">
          <div className="flex items-center gap-3 mb-2">
            <Music size={24} />
            <h3 className="text-lg font-semibold">Total d'écoutes</h3>
          </div>
          <p className="text-4xl font-bold">{stats.totalListens}</p>
        </div>

        <div className="bg-gradient-to-br from-purple-500 to-purple-700 rounded-lg p-6 text-white">
          <div className="flex items-center gap-3 mb-2">
            <TrendingUp size={24} />
            <h3 className="text-lg font-semibold">Titres uniques</h3>
          </div>
          <p className="text-4xl font-bold">{stats.uniqueTracks}</p>
        </div>

        <div className="bg-gradient-to-br from-green-500 to-green-700 rounded-lg p-6 text-white">
          <div className="flex items-center gap-3 mb-2">
            <Calendar size={24} />
            <h3 className="text-lg font-semibold">Moy. par jour</h3>
          </div>
          <p className="text-4xl font-bold">
            {Object.keys(stats.listensByDay).length > 0
              ? Math.round(stats.totalListens / Object.keys(stats.listensByDay).length)
              : 0}
          </p>
        </div>
      </div>

      {/* Graphique d'écoutes par jour */}
      {chartData.length > 0 && (
        <div className="bg-gray-100 dark:bg-gray-800 rounded-lg p-6 mb-8">
          <h2 className="text-xl font-semibold mb-4">Écoutes des 7 derniers jours</h2>
          <ResponsiveContainer width="100%" height={300}>
            <BarChart data={chartData}>
              <CartesianGrid strokeDasharray="3 3" stroke="#444" />
              <XAxis dataKey="date" stroke="#888" />
              <YAxis stroke="#888" />
              <Tooltip 
                contentStyle={{ 
                  backgroundColor: '#1f2937', 
                  border: '1px solid #374151',
                  borderRadius: '8px',
                  color: '#fff'
                }} 
              />
              <Bar dataKey="écoutes" fill="#3b82f6" />
            </BarChart>
          </ResponsiveContainer>
        </div>
      )}

      {/* Top tracks */}
      <div className="bg-gray-100 dark:bg-gray-800 rounded-lg p-6">
        <h2 className="text-xl font-semibold mb-4">Top 10 des titres les plus écoutés</h2>
        {stats.topTracks.length === 0 ? (
          <p className="text-gray-400 text-center py-8">Aucune écoute pour le moment</p>
        ) : (
          <div className="space-y-3">
            {stats.topTracks.map((item, index) => (
              <div
                key={item.track.id}
                className="flex items-center gap-4 p-3 bg-gray-50 dark:bg-gray-700 rounded-lg hover:bg-gray-200 dark:hover:bg-gray-600 transition-colors"
              >
                <div className="text-2xl font-bold text-gray-400 w-8 text-center">
                  {index + 1}
                </div>
                <div className="flex-1">
                  <div className="font-semibold">{item.track.title}</div>
                  <div className="text-sm text-gray-500 dark:text-gray-400">{item.track.artist}</div>
                </div>
                <div className="text-right">
                  <div className="font-bold text-blue-500">{item.playCount}</div>
                  <div className="text-xs text-gray-400">écoutes</div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}
